export function getScrollTop() {
  var scrollTop = 0;
  if (document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop;
  } else if(document.body) {
      scrollTop = document.body.scrollTop;
  }
  return scrollTop;
}

//Ëé∑ÂèñÂΩìÂâçÂèØËßÜËåÉÂõ¥ÁöÑÈ´òÂ∫¶  
export function getClientHeight() {
  var clientHeight = 0;
  if(document.body.clientHeight && document.documentElement.clientHeight) {
      clientHeight = Math.min(document.body.clientHeight, document.documentElement.clientHeight);
  } else {
      clientHeight = Math.max(document.body.clientHeight, document.documentElement.clientHeight);
  }
  return clientHeight;
}

//Ëé∑ÂèñÊñáÊ°£ÂÆåÊï¥ÁöÑÈ´òÂ∫¶ 
export function getScrollHeight() {
  return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight);
}

/**
 * Ëé∑Âèñ‚Äú‰∏Ä‰∏™‚ÄùÂõæÁâáÁöÑÂéüÂßãÂàõÂª∫Êó•Êúü 
 * @param {Number} qiniuKey 
 * @returns {Number} stamp
 */
export function getOnePicCreatedStamp(qiniuKey) {
  // console.log('qiniuKey', qiniuKey)
  if (!qiniuKey) {
    return;
  }
  qiniuKey = Number(qiniuKey)

  const todayStamp = new Date('2021-02-07').getTime();
  const oneDay = 24 * 60 * 60 * 1000;
  const todayKey = 3046;
  const stamp = todayStamp - (todayKey - qiniuKey) * oneDay

  return stamp;
}

// unixÊó∂Èó¥Êà≥Ê†ºÂºèÂåñÊòæÁ§∫
export function timeStampFormat(timestamp, fmt, defaultval) {
  if (!timestamp || timestamp == '0') {
    return defaultval || '';
  }
  const date = new Date();
  if ((`${timestamp}`).length == 10) {
    timestamp = timestamp * 1000;
  }
  date.setTime(timestamp);
  const o = {
    'M+': date.getMonth() + 1, // Êúà‰ªΩ
    'd+': date.getDate(), // Êó•
    'h+': date.getHours(), // Â∞èÊó∂
    'm+': date.getMinutes(), // ÂàÜ
    's+': date.getSeconds(), // Áßí
    'q+': Math.floor((date.getMonth() + 3) / 3), // Â≠£Â∫¶
    S: date.getMilliseconds(), // ÊØ´Áßí
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (`${date.getFullYear()}`).substr(4 - RegExp.$1.length));
  for (const k in o) {
    if (new RegExp(`(${k})`).test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : ((`00${o[k]}`).substr((`${o[k]}`).length)));
  }
  return fmt;
};


export function copyLink(value) {
  const input = document.createElement("input");
    input.value = value;
    input.style.opacity = 0;
    document.body.appendChild(input);
    input.select();
    document.execCommand("copy");
    document.body.removeChild(input);
}


// Âà§Êñ≠ÊòØÂê¶ÊòØPCÁ´Ø
export function isPC() {
  var userAgentInfo = navigator.userAgent;
  var Agents = ["Android", "iPhone",
              "SymbianOS", "Windows Phone",
              "iPad", "iPod"];
  var flag = true;
  for (var v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false;
          break;
      }
  }
  return flag;
}

